import axios from 'axios'

export default {
    //
    adminLogin(data){
        return axios.post('/api/login', data);
    },
    //
    adminLogout(){
        return axios.post('/api/logout')
    },
    //admin
    updAdminMes(data){
        return axios.post('/api/modifyUser', data);
    },
    //admin
    updAdminPwd(data){
        return axios.post('/api/modifyPwd', data);
    }
}


