import axios from 'axios'

export default {
    //
    createTag(data){
        return axios.post('/api/tags', data);
    },
    //
    getAllTags(){
        return axios.get('/api/tags');
    },
    //
    modifyTag(id, data){
        return axios.patch(`/api/tags/${ id }`, data);
    },
    //
    deleteTag(id){
        return axios.delete(`/api/tags/${ id }`);   
    }
}



